import { Component, OnInit, OnDestroy } from '@angular/core';
import { Headers, Http } from '@angular/http';
import { UserService } from '../../../service/user.service';

@Component({
  selector: 'app-hot-work',
  templateUrl: './hot-work.component.html',
  styleUrls: ['./hot-work.component.scss']
})
export class HotWorkComponent implements OnInit,OnDestroy {

  datas = [];
  eventSub;
  httpSub

  constructor(public http: Http, public userService: UserService) {
    this.getdata();
    this.eventSub = this.userService.hot_work_get_data.subscribe(() => {
      this.getdata();
    });
  }

  getdata() {
    
    let url = "https://www.devonhello.com/chihu2/hot_work";

    var headers = new Headers();
    headers.append('Content-Type', 'application/x-www-form-urlencoded');

    this.httpSub = this.http.post(url, "type=1" + "&len=" + this.datas.length , {
      headers: headers
    })
      .subscribe((res) => {
        
        this.datas = this.datas.concat(res.json());
      });
  }

  ngOnDestroy() {
    this.eventSub.unsubscribe();
    this.httpSub.unsubscribe();
  }

  ngOnInit() {
  }

}



